.navbar-dummy {
  background: linear-gradient(145deg, #9668e0 0%, #93bdec 100%);
  border-bottom: 2px solid white;
  width: 100%;
  height: 4vh;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.navbar-dummy a {
  margin: 0 2% 0 2%;
}

.navbar-logo {
  width: 28%;
  font-family: "HK Grotesk";
  font-size: 1.3em;
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.logo-group {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 0 0 0 4%;
}

.logo-group img {
  width: 34px;
  height: 28px;
  margin: 0 12px 0 0;
}

.logo-group p {
  margin: 0;
  padding: 0;
}

.navbar-panel {
  width: 30%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.navbar-panel p {
  color: white;
  margin: 0 4% 0 0;
}

.navbar-panel a {
  text-decoration: none;
  color: white;
  margin: 0 4% 0 0;
}

.logo-home {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.logo-home img {
  width: 34px;
  height: 28px;
  margin: 0 12px 0 10px;
}
